"use client";

import { useEffect, useState } from "react";
import { api } from "@/lib/axios"; 
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { 
  Plus, Search, FileText, Clock, AlertCircle, Sparkles, 
  LayoutGrid, List, Zap, MessageSquare, History 
} from "lucide-react";
import Link from "next/link";
import { Skeleton } from "@/components/ui/skeleton";

type Note = {
  id: string;
  title: string;
  updatedAt: string;
  previewText?: string;
};

export default function DashboardPage() {
  const [notes, setNotes] = useState<Note[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    async function fetchNotes() {
      try {
        setLoading(true);
        const res = await api.get("/documents");
        setNotes(Array.isArray(res.data) ? res.data : []);
      } catch (err) {
        setError("Could not load notes. Is the backend running?");
      } finally {
        setLoading(false);
      }
    }
    fetchNotes();
  }, []);

  const filteredNotes = notes.filter((note) =>
    note.title.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="container mx-auto px-6 py-10 max-w-7xl">
      {/* 1. HERO SECTION WITH STATS */}
      <div className="relative mb-12 overflow-hidden rounded-3xl bg-primary/5 p-8 border border-primary/10">
        <div className="relative z-10 flex flex-col md:flex-row md:items-center justify-between gap-6">
          <div className="space-y-2">
            <Badge className="bg-primary/20 text-primary hover:bg-primary/20 border-none px-3 py-1">
              <Sparkles className="mr-1.5 h-3 w-3" />
              Pro Workspace
            </Badge>
            <h1 className="text-4xl font-extrabold tracking-tight">CollabHub Central</h1>
            <p className="text-muted-foreground text-lg max-w-md">
              Your documents are synced across 3 devices. Ready to build something great?
            </p>
          </div>
          <div className="flex gap-3">
             <Button size="lg" className="h-12 px-6 shadow-lg shadow-primary/20 gap-2">
               <Plus className="h-5 w-5" /> New Document
             </Button>
          </div>
        </div>
        {/* Decorative Background Element */}
        <div className="absolute -right-10 -top-10 h-64 w-64 bg-primary/10 blur-3xl rounded-full" />
      </div>

      {/* 2. SEARCH & VIEW CONTROL */}
      <div className="mb-8 flex flex-col sm:flex-row gap-4 items-center justify-between">
        <Tabs defaultValue="grid" className="w-full sm:w-auto">
          <TabsList className="bg-muted/50 p-1">
            <TabsTrigger value="grid" className="gap-2"><LayoutGrid className="h-4 w-4" /> Grid</TabsTrigger>
            <TabsTrigger value="list" className="gap-2"><List className="h-4 w-4" /> List</TabsTrigger>
          </TabsList>
        </Tabs>

        <div className="relative w-full sm:w-80">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Search documents..."
            className="pl-10 h-11 bg-background border-muted-foreground/20 focus:border-primary transition-all"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
        </div>
      </div>

      {/* 3. BENTO GRID CONTENT */}
      <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
        
        {/* Main Feed */}
        <div className="lg:col-span-3 space-y-6">
          {error && (
            <div className="flex items-center gap-3 rounded-xl border border-destructive/50 bg-destructive/5 p-4 text-destructive">
              <AlertCircle className="h-5 w-5" />
              <p className="font-medium text-sm">{error}</p>
            </div>
          )}

          <div className="grid gap-6 sm:grid-cols-2">
            {loading ? (
              Array.from({ length: 4 }).map((_, i) => (
                <Skeleton key={i} className="h-[180px] w-full rounded-2xl" />
              ))
            ) : filteredNotes.length > 0 ? (
              filteredNotes.map((note) => (
                <Link key={note.id} href={`/dashboard/documents/${note.id}`}>
                  <Card className="group relative h-full bg-background hover:bg-accent/5 transition-all duration-300 border-border/60 hover:border-primary/50 overflow-hidden rounded-2xl">
                    <CardHeader className="pb-3">
                      <div className="flex justify-between items-start">
                        <div className="p-2 bg-primary/10 rounded-lg group-hover:bg-primary group-hover:text-primary-foreground transition-colors">
                          <FileText className="h-5 w-5" />
                        </div>
                        <Badge variant="outline" className="text-[10px] opacity-0 group-hover:opacity-100 transition-opacity">
                          Open →
                        </Badge>
                      </div>
                      <CardTitle className="pt-4 text-xl line-clamp-1">{note.title || "Untitled"}</CardTitle>
                      <CardDescription className="flex items-center gap-1.5">
                        <Clock className="h-3 w-3" />
                        {new Date(note.updatedAt).toLocaleDateString()}
                      </CardDescription>
                    </CardHeader>
                    <CardContent>
                       <p className="text-sm text-muted-foreground line-clamp-2 italic">
                        "{note.previewText || "Empty document awaiting your ideas..."}"
                       </p>
                    </CardContent>
                  </Card>
                </Link>
              ))
            ) : (
              <div className="col-span-full py-20 text-center">
                <div className="mx-auto w-16 h-16 bg-muted rounded-full flex items-center justify-center mb-4">
                  <Zap className="text-muted-foreground" />
                </div>
                <h3 className="text-lg font-semibold">No documents found</h3>
                <p className="text-muted-foreground">Try a different search or create a new one.</p>
              </div>
            )}
          </div>
        </div>

        {/* 4. SIDEBAR INSIGHTS (The "Impressive" part) */}
        <div className="space-y-6">
          <Card className="rounded-2xl border-primary/10 bg-gradient-to-b from-primary/5 to-transparent">
            <CardHeader>
              <CardTitle className="text-sm font-bold flex items-center gap-2">
                <Zap className="h-4 w-4 text-yellow-500 fill-yellow-500" />
                AI Assistant
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
               <div className="p-3 bg-background rounded-xl border text-xs leading-relaxed shadow-sm">
                 You have <span className="text-primary font-bold">3 notes</span> that haven't been summarized yet. Want me to do it?
               </div>
               <Button variant="outline" size="sm" className="w-full text-xs">Summarize All</Button>
            </CardContent>
          </Card>

          <Card className="rounded-2xl border-none bg-muted/30">
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-bold flex items-center gap-2">
                <History className="h-4 w-4" />
                Recent Activity
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
               {[1, 2].map((i) => (
                 <div key={i} className="flex gap-3">
                   <div className="h-8 w-8 rounded-full bg-blue-500/20 flex items-center justify-center text-blue-600 text-[10px] font-bold">JD</div>
                   <div className="space-y-0.5">
                     <p className="text-xs font-medium">John Doe edited</p>
                     <p className="text-[10px] text-muted-foreground">Project Alpha • 2m ago</p>
                   </div>
                 </div>
               ))}
            </CardContent>
          </Card>
        </div>

      </div>
    </div>
  );
}